<template>
	<v-tabs light background-color="grey lighten-3">
		<v-tab v-for="item in getLayout" :to="`/${item.toLowerCase()}`" :key="item">
			{{ item }}</v-tab
		>
		<v-spacer></v-spacer>
		<v-row no-gutters="" justify="end" align="center" class="mr-2">
			<v-btn class="mx-1" @click="openPieChart()" color="blue">Pie Chart</v-btn>
			<v-btn class="mx-1" @click="openBarChart()" color="green">Bar Chart</v-btn>
			<v-btn class="mx-1" @click="openTest()" color="red">Test</v-btn>
		</v-row>
	</v-tabs>
</template>

<script>
	import { mapMutations, mapState } from "vuex";
	export default {
		name: "AppNav",
		computed: {
			...mapState({
				appData: state => state.dataLoader.appData,
				layout: state => state.config.layout
			}),
			getLayout() {
				const arr = [];
				this.layout.forEach(item => {
					item === "Home" ? null : arr.push(item);
				});
				return arr;
			}
		},
		methods: {
			...mapMutations({
				SET_DIALOG_OBJ: "dialog/SET_DIALOG_OBJ"
			}),
			openPieChart() {
				this.SET_DIALOG_OBJ({
					component: "Charts/PieChart",
					visible: true,
					data: this.appData.geojson.sales,
					prop: "Payment_Type",
					id: "pie"
				});
			},
			openBarChart() {
				this.SET_DIALOG_OBJ({
					component: "Charts/BarChartStock",
					visible: true,
					data: this.appData.geojson.sales,
					prop: "Payment_Type",
					dateProp: "Transaction_date",
					id: "bar",
					width: "800px"
				});
			},
			openTest() {
				this.SET_DIALOG_OBJ({
					component: "UI-Other/Test",
					visible: true,
					text: "Hi Matt",
					data: this.appData.geojson.sales
				});
			}
		}
	};
</script>
<style lang="scss"></style>
