import Vue from "vue";
import _ from "lodash";
const d3 = require("d3");

const state = {
	storyData: null
};

const mutations = {
	SET_STORY_DATA: (state, obj) => {
		state.storyData = obj;
	}
};

const actions = {
	createStoryData: async ({ commit, rootState, dispatch }, obj) => {
		var results = obj.data.filter(item => {
			return obj.filterProps.every(c => obj.filterValues.indexOf(item.properties[c]) > -1);
		});
		const dataProp = rootState.config.data[obj.id].style.subProp;
		commit(
			"SET_STORY_DATA",
			_.groupBy(results, x => _.get(x.properties, dataProp))
		);
	}
};

export default {
	namespaced: true,
	state,
	actions,
	mutations
};
