<template>
	<v-app dark v-if="appReady && arcDataLoaded">
		<Dialog v-if="dialog" />
		<AppHeader v-if="currentRoute != 'Home'" :config="config" />
		<transition :name="transitionName">
			<keep-alive max="5">
				<router-view></router-view>
			</keep-alive>
		</transition>
		<AppFooter v-if="currentRoute != 'Home'" />
	</v-app>
</template>

<script>
	import AppHeader from "./core_components/Template/AppHeader";
	import Dialog from "./core_components/Template/Dialog.vue";
	import AppFooter from "./core_components/Template/AppFooter.vue";

	import { mapActions, mapState, mapMutations } from "vuex";

	export default {
		components: {
			AppHeader,
			Dialog,
			AppFooter
		},
		name: "App",
		data: () => ({
			appReady: false,
			count: 0,
			legend: true,
			currentRoute: "Home",
			myClass: "test",
			transitionName: "slide",
			legendOptions: {
				title: "Sales Legend",
				component: "Legend",
				id: "sales"
			},
			arcOptions: {
				title: "Arc Legend",
				component: "ArcLegend",
				id: "arc"
			}
		}),
		computed: {
			...mapState({
				config: state => state.config,
				appData: state => state.dataLoader.appData,
				dialog: state => state.dialog.dialogObj.visible,
				arcDataLoaded: state => state.arcgis.arcDataLoaded
			})
		},
		methods: {
			...mapActions({
				loadData: "dataLoader/loadData",
				createIconObj: "icons/createIconObj"
			}),
			...mapMutations({
				SET_DIALOG_OBJ: "dialog/SET_DIALOG_OBJ"
			}),
			openPieChart() {
				this.SET_DIALOG_OBJ({
					component: "PieChart",
					visible: true,
					data: this.appData.geojson.sales,
					prop: "Payment_Type",
					id: "pie"
				});
			},
			openBarChart() {
				this.SET_DIALOG_OBJ({
					component: "BarChartStock",
					visible: true,
					data: this.appData.geojson.sales,
					prop: "Payment_Type",
					dateProp: "Transaction_date",
					id: "bar",
					width: "800px"
				});
			},
			openTest() {
				this.SET_DIALOG_OBJ({
					component: "Test",
					visible: true,
					text: "Hi Matt",
					data: this.appData.geojson.sales
				});
			}
		},
		beforeMount() {
			const Keys = Object.keys(this.config.data);
			Keys.forEach((key, index) => {
				this.loadData(this.config.data[key]).finally(() => {
					this.count++;
					if (Keys.length === this.count) {
						this.appReady = true;
					}
				});
			});
		},
		watch: {
			$route: {
				handler: function(to, from) {
					console.log("ROUTES", to, from);
					this.currentRoute = to.name;
					to.name === "Home" ? "slide" : "fade";
				},
				deep: true,
				immediate: true
			}
		}
	};
</script>

<style lang="scss">
	html {
		overflow: hidden !important;
	}

	#test {
		padding: 0 !important;
	}

	#timeslider-container {
		position: absolute;
		bottom: 00px;
		left: 0;
		z-index: 10;
		width: 100%;
		height: 125px;
		padding: 0 50px;
		background: rgba(255, 255, 255, 0.6);
		display: flex;
		align-items: center;
	}

	#pieChart-container {
		position: absolute;
		bottom: 00px;
		left: 0;
		z-index: 10;
		width: 100%;
		height: 125px;
		padding: 0 50px;
		background: rgba(255, 255, 255, 0.6);
		display: flex;
		align-items: center;
	}

	.slide-enter-active {
		transition: all 0.3s ease;
	}
	.slide-leave-active {
		transition: all 0.3s ease-in-out;
	}
	.slide-enter,
	.slide-leave-to {
		transform: translateY(100vh);
	}

	.fade-enter-active {
		transition: opacity 0.3s ease;
	}
	.fade-leave-active {
		transition: opacity 0.3s ease-in-out;
	}
	.fade-enter,
	.fade-leave-to {
		opacity: 0;
	}
</style>
