<template>
	<v-dialog
		v-model="dialogObj.visible"
		@click:outside="closeDialog()"
		:width="dialogObj.width ? dialogObj.width : '500px'"
	>
		<component
			v-if="dialogComponent"
			:is="dialogComponent"
			ref="globalComponent"
			:dialogObj="dialogObj"
		/>
	</v-dialog>
</template>

<script>
	import { mapState, mapMutations } from "vuex";

	export default {
		name: "Dialog",
		data: () => ({
			dialogComponent: null
		}),
		computed: {
			...mapState({
				dialogObj: state => state.dialog.dialogObj
			})
		},
		methods: {
			...mapMutations({
				SET_DIALOG_OBJ: "dialog/SET_DIALOG_OBJ"
			}),
			closeDialog() {
				this.SET_DIALOG_OBJ({ visible: false });
			}
		},
		created() {
			const comp = this.dialogObj.component.split("/");
			const requireContext = require.context("../", true, /.*\.vue$/);
			const dynamicComponents = requireContext
				.keys()
				.map(file => [file.replace(/(^.\/)|(\.vue$)/g, ""), requireContext(file)])
				.reduce((components, [name, component]) => {
					if (name == this.dialogObj.component) {
						console.log("comp", component);
						this.$options.components.comp = component.default;
						this.dialogComponent = "comp";
					}
				}, {});
		}
	};
</script>
